.slider {
  grid-area: slider;
  display: none;
  @media (min-width: 1024px) {
    display: flex;
    min-width: 100%;
    min-height: 100%;
  }
  &__wrapper {
    position: relative;
    z-index: 20;
    display: flex;
    min-width: 100%;
    min-height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: 0.5s all;
    &--0 {
      transition: 0.5s all;
      background-image: url("../../../images/slider-1.jpg");
    }
    &--1 {
      transition: 0.5s all;
      background-image: url("../../../images/slider-2.jpg");
    }
    &--2 {
      transition: 0.5s all;
      background-image: url("../../../images/slider-3.jpg");
    }
    &--3 {
      transition: 0.5s all;
      background-image: url("../../../images/slider-4.jpg");
    }
  }
  &__container {
    position: relative;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    display: flex;
    min-width: 100%;
    min-height: 100%;
  }
  &__container.hidden {
    position: relative;
    background: rgba(17, 21, 24, 0.8);
  }
  &__container.hidden .arrow,
  &__container.hidden &-content {
    z-index: -1;
  }
}
