.arrow {
  display: block;
  position: relative;
  min-height: 100vh;
  background-color: transparent;
  transition: 0.2s all;
  min-width: 32px;
  @media (min-width: 1440px) {
    min-width: 64px;
  }
  &:hover {
    transition: 0.2s all;
    background-color: rgba(14, 194, 97, 0.2);
  }
  &__next {
    width: 10px;
    height: 20px;
    background-image: url("../../../../icons/rightArrow.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
  &__prev {
    width: 10px;
    height: 20px;
    background-image: url("../../../../icons/leftArrow.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}
